import React from 'react';

export interface ColGroupProps {
  /** заданная ширина колонки */
  colWidths: (number | string | undefined)[];

  /** счетчик кол-ва колонок */
  columnCount: number;
}

/**
 * @description Предназначен для задания ширины одной или нескольких колонок таблицы
 * @returns Тег colgroup c набором колонок с заданной шириной
 */
function ColGroup({ colWidths, columnCount }: ColGroupProps): JSX.Element {
  const cols: JSX.Element[] = [];

  for (let i = 0; i < columnCount; i += 1) {
    const width = colWidths[i];

    cols.push(<col key={ i } style={ { width: width ?? '0px' } } />);
  }

  return (
    <colgroup>
      { cols }
    </colgroup>
  );
}

export default ColGroup;
